.pList{
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.pListItem{
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
}

.pListImg{
    width: 100%;
    /* height: 150px; */
    object-fit: cover;
}

.pListTitles p:first-child{
    font-weight: bold;
}