#find {
  display: grid;
  width: 100%;
  padding-top: 50px;
  height: 150px;
  background-color: #f2f2f2;
  padding-bottom: 50px;
}

#find p {
  margin-top: -20px;
  color: rgb(105, 107, 107);
  font-size: 20px;
}
#find input {
  padding: 17px 18px;
  width: 700px;
  border: 5px solid #e8b21e;
  border-radius: 3px 0 0 3px;
  border-right-style: none;
}
#find input:focus {
  outline: none;
}
#find button {
  padding: 17.5px 35px;
  font-weight: 600;
  border: none;
  background-color: #0077cc;
  color: aliceblue;
  border: 5px solid #e8b21e;
  border-left-style: none;
  border-radius: 0 3px 3px 0;
  border-left: 6px solid #e8b21e;
}

#find > div {
  width: 50%;
  height: 100%;
  display: grid;
  margin: auto;
  align-items: center;
}

#find > div + div {
  display: flex;
}
#first_input {
  font-size: 14px;
}

#guidelines {
  display: flex;
  align-items: center;
  justify-content: left;
  border: 1px solid rgb(226, 223, 223);
  width: 70%;
  margin: auto;
  margin-top: 50px;
  padding: 5px;
  margin-bottom: 10px;
  padding-left: 10px;
}
#guidelines p {
  margin-left: 10px;
}

#Nearby_destinations {
  display: grid;
  width: 70%;
  margin: auto;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(200px);
  gap: 20px;
}
#Nearby_destinations p {
  margin-top: -55px;
  color: white;
  margin-left: 15px;
  font-weight: 600;
}

#Nearby_destinations img {
  width: 100%;
  height: 100%;
  border-radius: 2px;
}
#Nearby_destinations img:hover {
  transform: scale(1.05);
}

#Nearby {
  width: 70%;
  margin: auto;
}

#Sign_in_to_save_time {
  width: 70%;
  height: 110px;
  border: 1px solid rgb(226, 223, 223);
  margin: auto;
  line-height: 8px;
  margin-top: 10px;
  padding-left: 10px;
}

#you_covered {
  width: 70%;
  height: 170px;
  margin: auto;
  border: 1px solid rgb(226, 223, 223);
  margin-top: 50px;
  padding: 10px;
  border-right: none;
  border-left: none;
}

#you_covered_inner {
  width: 100%;
  height: 200px;
  margin-top: -50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 150px;
  margin: auto;
}

#Explore {
  width: 70%;
  height: 150px;
  margin: auto;
}
#Explore h3 {
  margin-bottom: -15px;
}
#Explore > div + div {
  display: flex;
  gap: 20px;
}
#Explore > div {
  margin-top: 40px;
  margin-bottom: -20px;
}
#Explore > div + div {
  margin-bottom: 50px;
}
#Explore_img {
  width: 70%;
  height: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(200px);
  gap: 15px;
  margin: auto;
  margin-top: -20px;
}
#Explore_img img {
  width: 100%;
  height: 100%;
  border-radius: 2px;
}
#Explore_img img:hover {
  transform: scale(1.05);
}
#Explore_img p:hover {
  transform: scale(1.05);
}
.link {
  text-decoration: none;
}
#aaa {
  font-size: 20px;
  font-weight: bolder;
}
#bbb {
  font-size: x-large;
  font-weight: bolder;
}
#inner_country p::after {
  content: "";
  width: 0%;
  height: 2px;
  background-color: blue;
  margin: auto;
  display: block;
}
#inner_country p:hover::after {
  width: 100%;
  transition: width 0.2s linear;
}
#Explore_img p {
  margin-top: -50px;
  margin-left: 10px;
  color: #f2f2f2;
  font-weight: bold;
}

@media all and (min-width: 320px) and (max-width: 480px) {
  #find {
    margin-left: -150px;
  }
  #guidelines {
    display: flex;
    margin-left: 100px;
  }
  #Nearby_destinations {
    display: grid;
    grid-template-columns: 200px;
    grid-template-rows: repeat(100px, 1fr);
  }
}
